@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
html {
  padding: 0;
}

*{scroll-behavior:smooth;font-family:'Roboto', sans-serif
}
a{color:#333;text-decoration:none
}
#App{max-width:1280px;margin:auto;padding-top:100px
}
#underline{height:1px;width:0;margin:auto;background-color:#30cfd0;-webkit-transition:all .3s ease-Out;transition:all .3s ease-Out
}
#menuContainer{position:fixed;right:0px;left:0px;display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:nowrap;flex-wrap:nowrap;-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end;padding:10px;background-color:#ffffffaa
}
.menuItem{cursor:pointer;margin-right:20px;margin-top:10px;font-weight:300
}
.menuIcon{cursor:pointer
}
.menuIconBar1,.menuIconBar2,.menuIconBar3{width:35px;height:3px;background-color:#333;-webkit-transition:0.3s;transition:0.3s;margin:8px;position:relative;border-radius:25px
}
.change .menuIconBar1{opacity:0
}
.change .menuIconBar2{-webkit-transform:rotate(-20deg) translate(-6px, 6px);transform:rotate(-20deg)
}
.change .menuIconBar3{-webkit-transform:rotate(225deg);transform:rotate(225deg) translate(8px, 8px);-webkit-transition-delay:0.2s;transition-delay:0.2s
}
#drawer{background-color:#fff;opacity:0.9;position:fixed;text-align:center;width:100%;right:0px;top:0px;bottom:0px;padding-top:20%
}
.drawerItem{cursor:pointer;padding:15px;font-family:'Krona One', sans-serif
}
.drawerItem:hover #underline{width:100px
}
.menuItem:hover #underline{width:100%
}
@media all and (min-width: 960px){
.menuItem{opacity:1
}
.menuIcon,#drawer{opacity:0;-webkit-transition:0.3s;transition:0.3s;visibility:hidden
}
}
@media all and (max-width: 960px){
.menuItem{opacity:0;-webkit-transition:0.3s;transition:0.3s;visibility:hidden
}
.menuIcon,#drawer{opacity:0.9
}
}

